.mask{
  width: 100%;
  height: 100vh;
  position: relative;
}

.intro-img{
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  object-fit: cover;
}

.mask::after{
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  position: absolute;
  background-color: black;
  opacity: 0.8;
}

.home{
  height: 100%;
  width: 100%;
}

.home .Content{
  position: absolute;
  transform: translate(-50%,-50%);
  top: 50%;
  left: 50%;
  text-align: center;
}

.home .Content h1{
  font-size: 4rem;
  padding: 0.6rem 0 1.5rem;
}

.home .Content p{
  font-size: 1.4rem;
  font-weight: 200;
  text-transform: uppercase;
}

.Content .btn{
  margin: 1rem 0.2rem;
}

@media screen and (max-width:640px) {
  .home .Content h1{
    font-size: 3rem;
  }
  
  .home .Content p{
    font-size: 1.4rem;
  }

  .Content .btn{
    margin: 1rem 0.2rem;
  }
}