.team-heading{
  text-align: center;
  padding: 4rem 0 2rem 0;
}

.team-container{
  max-width: 1140px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(3,1fr);
  grid-gap: 40px;
  padding-bottom: 5rem;
}

.team-card{
  background: #1a1919;
  padding: 1.2rem 1rem;
}

.team-card img{
  width: 100%;
  border-radius: 50%;
}

.team-name{
  color: white;
  padding: 1rem 0;
}

.team-details p{
  padding-bottom: 1rem;
  font-size: 1.1rem;
  text-align: justify;
}

.team-btns{
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
}

.team-btns .btn{
  padding: 0.5rem 1rem;
  margin: 0.5rem 1rem;
}

@media screen and (max-width:740px) {
  .team-container{
    max-width: 90%;
    margin: auto;
    grid-template-columns: 1fr;
  }
}